          label: '德语'
        },
        {
          value: '21',
          label: '法语'
        }
      ],
      // 粉丝标签
      fansTagItems: [],
      fansTotal: 0,
      tag_id: '',
      choiceType: ''
    }
  },
  mounted: function() {
    document.body.ondrop = function(event) {
      event.preventDefault()
      event.stopPropagation()
    }
  },
  methods: {
    selMediaData(i) {
      // 将选择当前以外数据清空
      this.formData.menuList[i].mediaData = null
      this.formData.menuList[i].keyword = []
      this.formData.menuList[i].linkUrl = ''
    },
    selMediaData2(i) {
      // 将选择当前以外数据清空
      this.formData.menuList[this.selectedMenuIndex].childs[i].mediaData = null
      this.formData.menuList[this.selectedMenuIndex].childs[i].keyword = []
      this.formData.menuList[this.selectedMenuIndex].childs[i].linkUrl = ''
    },
    datadragEnd(evt) {
      evt.preventDefault()
    },
    save() {
      this.update('save')
    },
    getFormData(id) {
      this.loading = true
      // 通过id获取表单信息
      menuManageGet({ id: id }).then(res => {
        var arr = res.data
        if (res.data.tagId !== '') {
          res.data.tagId = parseInt(res.data.tagId)
        }
        arr.menuList.forEach(v => {
          if (v.childs && v.childs.length > 0) {
            v.childs.forEach(e => {
              if (e.menuType === 'media_id') {
                // e.type = 1
                e.mediaData = e.material
                e.mediaId = e.material.mediaId
              } else if (e.menuType === 'view') {
                // e.type = 2
              } else if (e.menuType === 'click') {
                // e.type = 3
                e.keyword = [e.wechatReplyContent]
                e.menuKey = e.wechatReplyContent.id
              } else if (e.menuType === 'miniprogram') {
                // e.type = 9
              }
            })
          } else {
            if (v.menuType === 'media_id') {
              // v.type = 1
              v.mediaData = v.material
              v.mediaId = v.material.mediaId
            } else if (v.menuType === 'view') {
              // v.type = 2
            } else if (v.menuType === 'click') {
              // v.type = 3
              v.keyword = [v.wechatReplyContent]
              v.menuKey = v.wechatReplyContent.id
            } else if (v.menuType === 'miniprogram') {
              // v.type = 9
            }
          }
        })
        if (res.data.area) {
          this.getArea(res.data.area)
        }
        this.formData = Object.assign(defaultData, arr)
        this.formData.status = arr.status
        this.loading = false
      })
    },
    getArea(data) {
      if (data.parent) {
        if (data.parent.parent) {
          this.areaInfo.push(data.parent.parent.areaCode)
        }
        this.areaInfo.push(data.parent.areaCode)
      }
      this.areaInfo.push(data.areaCode)
    },
    update(type) {
      // 修改
      this.$refs.form.validate((valid, item) => {
        if (valid) {
          var params = {
            id: this.$route.query.id,
            menuGroupType: 2,
            areaCode: this.formData.areaCode,
            tagId: this.formData.tagId,
            sexDictCode: this.formData.sexDictCode,
            clientDictCode: this.formData.clientDictCode,
            languageDictCode: this.formData.languageDictCode,
            status: this.formData.status,
            menuGroupName: this.formData.menuGroupName,
            menuList: []
          }
          var sel = true
          this.formData.menuList.forEach((v, i) => {
            if (v.childs && v.childs.length > 0) {
              if (v.menuName === '') {
                this.$errorMessage('请填写菜单名称')
                this.selectedMenu(i)
                sel = false
              }
              v.childs.forEach((e, j) => {
                if (e.menuName === '') {
                  this.$errorMessage('请填写菜单名称')
                  this.selectedMenuIndex = i
                  this.selectedSubMenu(j)
                  sel = false
                }
                if (e.menuType === 'click') {
                  if (!e.menuKey || e.menuKey === '') {
                    this.$errorMessage('请选择关键词回复内容')
                    this.selectedMenuIndex = i
                    this.selectedSubMenu(j)
                    sel = false
                  }
                } else if (e.menuType === 'media_id') {
                  if (!e.mediaId || e.mediaId === '') {
                    this.$errorMessage('请选择回复素材')
                    this.selectedMenuIndex = i
                    this.selectedSubMenu(j)
                    sel = false
                  }
                } else if (e.menuType === 'view') {
                  if (!e.linkUrl || e.linkUrl === '') {
                    this.$errorMessage('请填写跳转url')
                    this.selectedMenuIndex = i
                    this.selectedSubMenu(j)
                    sel = false
                  }
                } else if (e.menuType === 'miniprogram') {
                  if (!e.linkUrl || e.linkUrl === '') {
                    this.$errorMessage('请填写小程序跳转url')
                    this.selectedMenuIndex = i
                    this.selectedSubMenu(j)
                    sel = false
                  }
                  if (!e.miniprogramAppid || e.miniprogramAppid === '') {
                    this.$errorMessage('请填写小程序appid')
                    this.selectedMenuIndex = i
                    this.selectedSubMenu(j)
                    sel = false
                  }
                  if (!e.miniprogramPagepath || e.miniprogramPagepath === '') {
                    this.$errorMessage('请填写小程序页面路径')
                    this.selectedMenuIndex = i
                    this.selectedSubMenu(j)
                    sel = false
                  }
                }
                delete e.mediaData
                delete e.keyword
                delete e.material
                delete e.wechatReplyContent
                delete e.id
              })
              delete v.menuType
              delete v.menuKey
              delete v.mediaId
              delete v.linkUrl
              delete v.miniprogramAppid
              delete v.miniprogramPagepath
              delete v.choiceType
            } else {
              if (v.menuType === 'click') {
                if (!v.menuKey || v.menuKey === '') {
                  this.$errorMessage('请选择关键词回复内容')
                  this.selectedMenu(i)
                  sel = false
                }
              } else if (v.menuType === 'media_id') {
                if (!v.mediaId || v.mediaId === '') {
                  this.$errorMessage('请选择回复素材')
                  this.selectedMenu(i)
                  sel = false
                }
              } else if (v.menuType === 'view') {
                if (!v.linkUrl || v.linkUrl === '') {
                  this.$errorMessage('请填写跳转url')
                  this.selectedMenu(i)
                  sel = false
                }
              } else if (v.menuType === 'miniprogram') {
                if (!v.linkUrl || v.linkUrl === '') {
                  this.$errorMessage('请填写小程序跳转url')
                  this.selectedMenu(i)
                  sel = false
                }
                if (!v.miniprogramAppid || v.miniprogramAppid === '') {
                  this.$errorMessage('请填写小程序appid')
                  this.selectedMenu(i)
                  sel = false
                }
                if (!v.miniprogramPagepath || v.miniprogramPagepath === '') {
                  this.$errorMessage('请填写小程序页面路径')
                  this.selectedMenu(i)
                  sel = false
                }
              }
            }
            delete v.mediaData
            delete v.keyword
            delete v.material
            delete v.wechatReplyContent
            delete v.id
          })
          if (sel) {
            params.menuList = this.formData.menuList
            if (type === 'save') {
              delete params.id
              this.saveForm(menuManageSave, params, true, '/wechatMenuMange/list', 'list')
            } else {
              this.updateForm(
                menuManageUpdate,
                params,
                '/wechatMenuMange/list',
                'list'
              )
            }
          } else {
            return
          }
        }
      })
    },
    // 选中主菜单
    selectedMenu(i, event) {
      this.selectedSubMenuIndex = ''
      this.selectedMenuIndex = i
      var selectedMenu = this.formData.menuList[this.selectedMenuIndex]
      // 清空选中media_id 防止再次请求
      if (
        selectedMenu.media_id !== undefined &&
        selectedMenu.media_id !== '' &&
        this.selectedMenuType(selectedMenu) === 'media_id'
      ) {
        this.getMaterial(selectedMenu.media_id)
      }
      // 检查名称长度
      this.checkMenuName(selectedMenu.menuName)
    },
    // 选中子菜单
    selectedSubMenu: function(i, event) {
      this.selectedSubMenuIndex = i
      var selectedSubMenu = this.formData.menuList[this.selectedMenuIndex]
        .childs[this.selectedSubMenuIndex]
      if (
        selectedSubMenu.media_id !== undefined &&
        selectedSubMenu !== '' &&
        this.selectedMenuType(selectedSubMenu) === 'media_id'
      ) {
        this.getMaterial(selectedSubMenu.media_id)
      }
      this.checkMenuName(selectedSubMenu.menuName)
    },
    getMediaData(items) {
      // 获取子菜单选择素材
      this.formData.menuList[this.selectedMenuIndex].childs[
        this.selectedSubMenuIndex
      ].mediaData =
        items[0]
      this.formData.menuList[this.selectedMenuIndex].childs[
        this.selectedSubMenuIndex
      ].mediaId =
        items[0].mediaId
    },
    getMediaDataTwo(items) {
      // 获取主菜单选择素材
      this.formData.menuList[this.selectedMenuIndex].mediaData = items[0]
      this.formData.menuList[this.selectedMenuIndex].mediaId = items[0].mediaId
    },
    getKeywordTwo(items) {
      // 获取主菜单关键字
      this.formData.menuList[this.selectedMenuIndex].keyword = items
      this.formData.menuList[this.selectedMenuIndex].menuKey = items[0].id
    },
    getKeyword(items) {
      // 获取子菜单关键字
      this.formData.menuList[this.selectedMenuIndex].childs[
        this.selectedSubMenuIndex
      ].keyword = items
      this.formData.menuList[this.selectedMenuIndex].childs[
        this.selectedSubMenuIndex
      ].menuKey =
        items[0].id
    },
    // 地区数据
    getAreaInfo(value) {
      this.formData.areaCode = value[0].code
      this.formData.provinceName = value[0].areaName
      if (value.length > 1) {
        this.formData.areaCode = value[1].code
        this.formData.cityName = value[1].areaName
      }
      if (value.length > 2) {
        this.formData.areaCode = value[2].code
        this.formData.areaName = value[2].areaName
      }
    },
    getFansTags() {
      // 获取粉丝标签
      weChatFansTags().then(res => {
        this.fansTagItems = res.data.tagList
        this.fansTotal = res.data.fansSize
      })
    },
    // 选中菜单级别
    selectedMenuLevel() {
      if (this.selectedMenuIndex !== '' && this.selectedSubMenuIndex === '') {
        // 主菜单
        return 1
      } else if (this.selectedSubMenuIndex !== '') {
        // 子菜单
        return 2
      } else {
        // 未选中任何菜单
        return 0
      }
    },
    // 获取菜单类型 1. view网页类型，2. media_id类型和view_limited类型 3. click点击类型，4.miniprogram表示小程序类型
    selectedMenuType: function(item) {
      var arr = [
        '',
        'media_id',
        'view',
        'click',
        'click',
        'click',
        'click',
        'click',
        'click',
        'miniprogram'
      ]
      if (item.choiceType === undefined) {
        item.choiceType = 1
      }
      item.menuType = arr[parseInt(item.choiceType)]
      return arr[parseInt(item.choiceType)]
    },
    // 添加菜单
    addMenu: function(level) {
      if (level === 1 && this.formData.menuList.length < 3) {
        this.formData.menuList.push({
          menuType: 'media_id',
          menuName: '菜单名称',
          childs: [],
          linkUrl: '',
          choiceType: 1,
          mediaData: null,
          keyword: []
        })
        this.selectedMenuIndex = this.formData.menuList.length - 1
        this.selectedSubMenuIndex = ''
      }
      if (
        level === 2 &&
        this.formData.menuList[this.selectedMenuIndex].childs.length < 5
      ) {
        this.formData.menuList[this.selectedMenuIndex].childs.push({
          menuType: 'media_id',
          menuName: '子菜单名称',
          linkUrl: '',
          choiceType: 1,
          mediaData: null,
          keyword: []
        })
        this.selectedSubMenuIndex =
          this.formData.menuList[this.selectedMenuIndex].childs.length - 1
      }
    },
    // 删除菜单
    delMenu: function() {
      if (this.selectedMenuLevel() === 1) {
        this.$confirm('删除后菜单下设置的内容将被删除', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          if (this.selectedMenuIndex === 0) {
            this.formData.menuList.splice(this.selectedMenuIndex, 1)
            this.selectedMenuIndex = 0
          } else {
            this.formData.menuList.splice(this.selectedMenuIndex, 1)
            this.selectedMenuIndex -= 1
          }
          if (this.formData.menuList.length === 0) {
            this.selectedMenuIndex = ''
          }
        })
      } else if (this.selectedMenuLevel() === 2) {
        this.$confirm('确定要删除此子菜单吗', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          if (this.selectedSubMenuIndex === 0) {
            this.formData.menuList[this.selectedMenuIndex].childs.splice(
              this.selectedSubMenuIndex,
              1
            )
            this.selectedSubMenuIndex = 0
          } else {
            this.formData.menuList[this.selectedMenuIndex].childs.splice(
              this.selectedSubMenuIndex,
              1
            )
            this.selectedSubMenuIndex -= 1
          }
          if (
            this.formData.menuList[this.selectedMenuIndex].childs.length === 0
          ) {
            this.selectedSubMenuIndex = ''
          }
        })
      }
    },
    // 检查菜单名称长度
    checkMenuName: function(val) {
      if (this.selectedMenuLevel() === 1 && this.getMenuNameLen(val) <= 8) {
        this.menuNameBounds = false
      } else if (
        this.selectedMenuLevel() === 2 &&
        this.getMenuNameLen(val) <= 16
      ) {
        this.menuNameBounds = false
      } else {
        this.menuNameBounds = true
      }
    },
    // 获取菜单名称长度
    getMenuNameLen: function(val) {
      var len = 0
      for (var i = 0; i < val.length; i++) {
        var a = val.charAt(i)
        a.match(/[^\x00-\xff]/gi) != null ? (len += 2) : (len += 1)
      }
      return len
    },
    // 获取素材信息
    getMaterial: function(id) {
      this.setTagsVisible = true
    }
  },
  created() {
    this.type = this.$route.query.type
    this.getFormData(this.$route.query.id)
    this.getFansTags()
  }
}
</script>
<style scoped>
.select_width {
  width: 188.5px !important;
}

.el-input--suffix {
  width: 120px !important;
}
</style>




